.tab-list {
    width: 20%;
    border-bottom: 1px solid #ccc;
    padding-left: 0;
}

.tab-list-item {
display: block;
list-style: none;
margin-bottom: -1px;
padding: 0.5rem 0.75rem;
}

.tab-list-active {
background-color: white;
border: solid #ccc;
border-width: 1px 1px 0 1px;
}

.tab-content {
    margin-left: 20%;
}

  
  